import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';


interface AlertDefineResidentialPhysicalStructureProperties {
    title: string;
    content: string;
}

const DEFAULT_CONTENT: string = 'Primero defina la estructura fsica residencial en configuracin.';

@Component({
    selector: 'hel-alert-define-residential-physical-structure-helisa',
    templateUrl: './alert-define-residential-physical-structure-helisa.component.html',
    styleUrls: ['./alert-define-residential-physical-structure-helisa.component.sass']
})
export class AlertDefineResidentialPhysicalStructureHelisaComponent implements OnInit {

    content: string;

    constructor(
        public dialogRef: MatDialogRef<AlertDefineResidentialPhysicalStructureHelisaComponent>,
        @Inject(MAT_DIALOG_DATA) public data: AlertDefineResidentialPhysicalStructureProperties
    ) {
        this.content = data.content;
        if (this.content === undefined) {
            this.content = DEFAULT_CONTENT;
        }
        dialogRef.disableClose = true;
        dialogRef.keydownEvents().subscribe((event: KeyboardEvent) => {
            if (event.code === 'Escape') {
                this.dialogRef.close(this.onCancel());
            }
        });
    }

    ngOnInit(): void {
        setTimeout(() => {
            this.dialogRef.close();
        }, 3000);
    }

    onCancel(): void {
        this.dialogRef.close();
    }
}
