import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs';
import {AlertAuthorizationTransactionHelisaComponent} from './alert-authorization-transaction-helisa.component';

@Injectable({
  providedIn: 'root'
})
export class AlertAuthorizationTransactionHelisaService {

  constructor(public dialog: MatDialog) { }

  openDialog(title?: string, content?: string, okLabel?: string, cancelLabel?: string): Observable<boolean> {
    const dialogRef: MatDialogRef<AlertAuthorizationTransactionHelisaComponent> = this.dialog.open(AlertAuthorizationTransactionHelisaComponent, {
      width: '250px',
      data: { title, content, okLabel, cancelLabel }
    });

    return dialogRef.afterClosed();
  }
}
