import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

const DEFAULT_CONTENT: string = 'Esta seguro que desea eliminar esta informacin?';

@Component({
  selector: 'hel-alert-confirm-delete-data-helisa',
  templateUrl: './alert-confirm-delete-data-helisa.component.html',
  styleUrls: ['./alert-confirm-delete-data-helisa.component.sass']
})
export class AlertConfirmDeleteDataHelisaComponent implements OnInit {

  title: string;
  content: string;
  okLabel: string;
  cancelLabel: string;

  constructor(
    public dialogRef: MatDialogRef<AlertConfirmDeleteDataHelisaComponent>,
    @Inject(MAT_DIALOG_DATA) public data: AlertConfirmDeleteDataHelisaComponent
  ) {
    this.content = data.content;
    if (this.content === undefined) {
      this.content = DEFAULT_CONTENT;
    }
    this.okLabel = data.okLabel;
    if (this.okLabel === undefined) {
      this.okLabel = 'Lo asumo';
    }
    this.cancelLabel = data.cancelLabel;
    if (this.cancelLabel === undefined) {
      this.cancelLabel = 'Me retracto';
    }
    dialogRef.disableClose = true;
    dialogRef.keydownEvents().subscribe((event: KeyboardEvent) => {
      if (event.code === 'Escape') {
        this.dialogRef.close(this.onCancel());
      }
    });
  }

  ngOnInit(): void {
  }

  onCancel(): void {
    this.dialogRef.close();
  }
}
