import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs';
import { AlertDefineResidentialPhysicalStructureHelisaComponent } from './alert-define-residential-physical-structure-helisa.component';



@Injectable({
  providedIn: 'root'
})
export class AlertDefineResidentialPhysicalStructureHelisaService {

  constructor(public dialog: MatDialog) { }

  openDialog(title?: string, content?: string): Observable<boolean> {
    const dialogRef: MatDialogRef<AlertDefineResidentialPhysicalStructureHelisaComponent> = this.dialog.open(AlertDefineResidentialPhysicalStructureHelisaComponent, {
      width: '250px',
      data: { title, content}
    });

    return dialogRef.afterClosed();
  }
}
