import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs';
import { AlertDefineCommercialStructureHelisaComponent } from './alert-define-commercial-structure-helisa.component';



@Injectable({
  providedIn: 'root'
})
export class AlertDefineCommercialStructureHelisaService {

  constructor(public dialog: MatDialog) { }

  openDialog(title?: string, content?: string): Observable<boolean> {
    const dialogRef: MatDialogRef<AlertDefineCommercialStructureHelisaComponent> = this.dialog.open(AlertDefineCommercialStructureHelisaComponent, {
      width: '250px',
      data: { title, content}
    });

    return dialogRef.afterClosed();
  }
}
