import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs';
import { AlertDefineParkingStructureHelisaComponent } from './alert-define-parking-structure-helisa.component';



@Injectable({
  providedIn: 'root'
})
export class AlertDefineParkingStructureHelisaService {

  constructor(public dialog: MatDialog) { }

  openDialog(title?: string, content?: string): Observable<boolean> {
    const dialogRef: MatDialogRef<AlertDefineParkingStructureHelisaComponent> = this.dialog.open(AlertDefineParkingStructureHelisaComponent, {
      width: '250px',
      data: { title, content}
    });

    return dialogRef.afterClosed();
  }
}
