import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';

interface AlertAuthorizationTransactionHelisaProperties {
  title: string;
  okLabel: string;
  cancelLabel: string;
}

const DEFAULT_TITLE: string = '!Esta transaccin requiere autorizacin!';

@Component({
  selector: 'hel-alert-authorization-transaction-helisa',
  templateUrl: './alert-authorization-transaction-helisa.component.html',
  styleUrls: ['./alert-authorization-transaction-helisa.component.sass']
})
export class AlertAuthorizationTransactionHelisaComponent implements OnInit {

  title: string;
  okLabel: string;
  cancelLabel: string;

  constructor(
    public dialogRef: MatDialogRef<AlertAuthorizationTransactionHelisaComponent>,
    @Inject(MAT_DIALOG_DATA) public data: AlertAuthorizationTransactionHelisaProperties
  ) {
    this.title = data.title;
    if (this.title === undefined) {
      this.title = DEFAULT_TITLE;
    }
    this.okLabel = data.okLabel;
    if (this.okLabel === undefined) {
      this.okLabel = 'Solicitarla';
    }
    this.cancelLabel = data.cancelLabel;
    if (this.cancelLabel === undefined) {
      this.cancelLabel = 'Negarla';
    }
    dialogRef.disableClose = true;
    dialogRef.keydownEvents().subscribe((event: KeyboardEvent) => {
      if (event.code === 'Escape') {
        this.dialogRef.close(this.onCancel());
      }
    });
  }

  ngOnInit(): void  {
  }

  onCancel(): void {
    this.dialogRef.close();
  }
}
